<template>
    <hr class="my-4" />
    <!-- 공지 번호 보여주는칸 -->
    <div id="noticeNum"> 
        <span>{{ notice.nonum }}</span>
    </div>
    <!-- 공지 제목 보여주는칸 -->
    <div id="noticeTitle"> 
        <h3>{{ notice.notitle }}</h3>
    </div>
    <!-- 공지게시 시간 보여주는칸 -->
    <div id="noticeDate">
        <span>{{ formatDate(notice.nodate) }}</span>
    </div>
    <hr class="my-4" />
    <!-- 공지 내용 보여주는칸 -->
    <div id="noticeContent">
        <span>{{ notice.nocon }}</span>
    </div> 
    <hr class="my-4" />
    <!-- 공지 삭제버튼과 수정버튼 -->
    <div class="noticeButton">
        <button id="noticeDelete" type="button" class="btn btn-dark me-2">공지사항 삭제하기</button>
        <button id="noticeModify" type="button" class="btn btn-primary" @click="moveModify(notice.nonum)">공지사항 수정하기</button>   
    </div>    
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            notice: {} // 공지사항 세부 정보를 저장할 객체
        };
    },
    // 공지 세부사항 마운트
    mounted() {
        this.fetchNoticeDetails();
    },
    methods: {
        async fetchNoticeDetails() {
            try {
                // HTTP POST 요청을 통해 공지사항 세부 정보를 가져옴
                const response = await axios.post('/JY_A_NoticeDetails', { nonum: this.$route.query.nonum }); 
                // 가져온 세부 정보를 저장
                this.notice = response.data;
                console.log('Response:', response.data);

            } catch (error) {
                console.error('Error fetching notice details:', error);
            }
        },
        // 날짜를 "YYYY-MM-DD HH:MM" 형식으로 포맷하여 반환
        formatDate(date) {
        const d = new Date(date);
        return `${d.toISOString().slice(0, 16).replace('T', ' ')}`;
        },
        // 공지 수정페이지로 이동
        async moveModify(nonum) {
            try {
                // 라우터를 사용하여 페이지 이동
                this.$router.push({ path: "/JY_A_NoticeModify", query: { nonum } });
            } catch (error) {
                console.error('Error fetching noticeDetails:', error);
            }
        }
    }
}
</script>